import Typography from '@mui/material/Typography'
import Button from '@mui/material/Button';

const ItemPicker = ({ selected, image, text, onClick }) => {
  return (
    <Button variant={selected?'outlined':'text'} style={{ height: 80, width: '100%', display: 'flex', flexDirection: 'column', alignContent: 'center', alignItems: 'center', padding: 0}} onClick={onClick}>
      <img alt="" style={{ height: 60, width:'100%'}}/>
      <Typography variant="caption" component="div" color={"text.secondary"}>
          {text}
      </Typography>
    </Button>
  );
}

export default ItemPicker;