import * as React from 'react';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogTitle from '@mui/material/DialogTitle';
import IconButton from '@mui/material/IconButton';
import SquareIcon from '@mui/icons-material/Square';
import Typography from '@mui/material/Typography';


const ColorSelect = ({ setSelectedColor, defaultColor, ignore=[] }) => {
  const colorList = [
    "red",
    "orange",
    "yellow",
    "green",
    "tiffany",
    "light-blue",
    "blue",
    "brown",
    "pupple",
    "pink",
    "white",
    "gray",
    "black",
    "texture-black"
  ]
  const colorMap = {
    "red": {
      "color": "#E90924",
      "display_name": "红色"
    },
    "orange": {
      "color": "#FF6512",
      "display_name": "橙色"
    },
    "yellow": {
      "color": "#FBEF12",
      "display_name": "黄色"
    },
    "green": {
      "color": "#76F720",
      "display_name": "绿色"
    },
    "tiffany": {
      "color": "#0BD4AC",
      "display_name": "蒂芙尼蓝"
    },
    "light-blue": {
      "color": "#169DE0",
      "display_name": "蓝色"
    },
    "blue": {
      "color": "#2C33C7",
      "display_name": "宝蓝色"
    },
    "brown": {
      "color": "#422A1A",
      "display_name": "褐色"
    },
    "pupple": {
      "color": "#863CB5",
      "display_name": "紫色"
    },
    "pink": {
      "color": "#F9CDDA",
      "display_name": "粉色"
    },
    "white": {
      "color": "#FFFFFF",
      "display_name": "白色"
    },
    "gray": {
      "color": "#7D7D7D",
      "display_name": "灰色"
    },
    "black": {
      "color": "#212121",
      "display_name": "黑色哑光"
    },
    "texture-black": {
      "color": "#333333",
      "display_name": "压纹黑"
    }
  }
  const [open, setOpen] = React.useState(false);

  const handleSelectButtonClick = (color) => {
    setSelectedColor(color);
    setOpen(false);
  }
  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = (event, reason) => {
    if (reason !== 'backdropClick') {
      setOpen(false);
    }
  };

  return (
    <div>
      <Button variant="text" aria-label={colorMap[defaultColor].display_name} size="large" onClick={handleClickOpen} style={{ color: 'black' }} startIcon={<SquareIcon fontSize="inherit" style={{ color: colorMap[defaultColor].color, border: '1px solid rgba(0, 0, 0, 0.12)' }}/>}>
        <Typography variant="subtitle1">{colorMap[defaultColor].display_name}</Typography>
      </Button>
      <Dialog disableEscapeKeyDown open={open} onClose={handleClose}>
        <DialogTitle>请选择颜色</DialogTitle>
        <DialogContent>
          <Box component="form" sx={{ display: 'flex', flexWrap: 'wrap' }}>
            {colorList.filter(item => !ignore.includes(item)).map((colorItem) => (
              <IconButton key={colorItem} aria-label={colorMap[colorItem].display_name} size="large" onClick={() => handleSelectButtonClick(colorItem)}>
                <SquareIcon fontSize="inherit" style={{ color: colorMap[colorItem].color, border: '1px solid rgba(0, 0, 0, 0.12)' }}/>
              </IconButton>
            ))}
          </Box>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose}>关闭</Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}

export default ColorSelect;