import * as React from 'react';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import SaveIcon from '@mui/icons-material/Save';
import TextField from '@mui/material/TextField';
import {encode, decode} from 'base-64';

const SaveLoadPopUp = ({ config, setConfig }) => {
  const [open, setOpen] = React.useState(false);
  const [hashConfig, setHashConfig] = React.useState(encode(JSON.stringify(config)));
  
  const handleLoad = () => {
    try {
      let configStr = decode(hashConfig)
      setConfig(JSON.parse(configStr));
      setOpen(false);
    } catch {
      alert('无法载入');
    }
    
  }
  const handleClickOpen = () => {
    setHashConfig(encode(JSON.stringify(config)))
    setOpen(true);
  };

  const handleClose = (event, reason) => {
    if (reason !== 'backdropClick') {
      setOpen(false);
    }
  };

  const onChange = (event) => {
    setHashConfig(event.target.value);
  }

  return (
    <div>
      <Button variant="outlined" onClick={handleClickOpen} startIcon={<SaveIcon />}>
        保存
      </Button>
      <Dialog disableEscapeKeyDown open={open} onClose={handleClose}>
        <DialogContent>
          <TextField
            id="standard-multiline-flexible"
            label="请选保存代码"
            multiline
            maxRows={10}
            onChange={onChange}
            value={hashConfig}
            variant="standard"
            style={{width:'280px'}}
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={handleLoad}>载入</Button>
          <Button onClick={handleClose}>关闭</Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}

export default SaveLoadPopUp;