import React, { useState } from 'react';
import { Button, Typography, Container, Divider, Drawer } from '@mui/material';
import EditIcon from '@mui/icons-material/Edit';
import CloseIcon from '@mui/icons-material/Close';
import DeleteIcon from '@mui/icons-material/Delete';
import ColorSelect from './ColorSelect'
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import Fab from '@mui/material/Fab';
import Box from '@mui/material/Box';
import Grid from '@mui/material/Unstable_Grid2';
import FormGroup from '@mui/material/FormGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';
import Paper from '@mui/material/Paper';
import Hood from './Hood'
import ItemPicker from './ItemPicker'
import qrcode from './qrcode.png'
import SaveLoadPopUp from './SaveLoadPopUp'

function App() {
  const defaultConfig = {
    "hood-base-color": "black",
    "crown-color": "black",
    "brow-color": "white",
    "bridge-color": "white",
    "nose-color": "black",
    "muzzle-color": "white",
    "muzzle-lip-color": "white",
    "chin-color": "black",
    "ear-a-color": "white",
    "ear-b-color": "black",
    "ear-c-color": "black",
    "muzzle-a-color": "white",
    "muzzle-b-color": "white",
    "muzzle-button": "black",
    "muzzle-style": "default", // ["default", "fur", "added-default", "added-fur"]
    "muzzle-assoicate": "default", // ["default", "mustache", "fur"]
    "eyebrow-style": "default", // ["default", "dou", "frame"]
    "glass-hole": false,
    "ear-hole": false,
    "texture-nose": false
  };

  const [drawerOpen, setDrawerOpen] = useState(false);
  const [config, setConfig] = useState(defaultConfig);

  const handleDrawerOpen = () => {
    setDrawerOpen(true);
  };

  const handleDrawerClose = () => {
    setDrawerOpen(false);
  };

  const handleResetToDefault = () => {
    setConfig(defaultConfig);
  };

  const handleSelectColor = (key, color) => {
    var newConfig = {...config}
    newConfig[key] = color
    setConfig(newConfig);
  };

  const handleCheckChange = (key, checked) => {
    var newConfig = {...config}
    newConfig[key] = checked
    setConfig(newConfig);
  };

  const handleEyeBrowStyleChange = (style) => {
    var newConfig = {...config}
    newConfig['eyebrow-style'] = style;
    setConfig(newConfig);
  };

  const handleMuzzleStyleChange = (style) => {
    var newConfig = {...config}
    if (newConfig['muzzle-style'].startsWith('added')) {
      newConfig['muzzle-style'] = 'added-' + style;
    } else {
      newConfig['muzzle-style'] = style;
    }
    setConfig(newConfig);
  };

  const handleMuzzleAddedChange = (added) => {
    var newConfig = {...config}
    if (added) {
      newConfig['muzzle-style'] = 'added-' + newConfig['muzzle-style'];
    } else {
      newConfig['muzzle-style'] = newConfig['muzzle-style'].replace('added-', '');
    }
    setConfig(newConfig);
  };

  const handleMuzzleAssoicateChange = (style) => {
    var newConfig = {...config}
    newConfig['muzzle-assoicate'] = style;
    setConfig(newConfig);
  }

  return (
    <Container>
      {/* Menu Icon */}
      <Fab size="large" color="primary" sx={{
        position: 'absolute',
        bottom: 20,
        right: 20
      }} aria-label="menu" onClick={handleDrawerOpen}>
        <EditIcon />
      </Fab>
      <Hood config={config}/>
      <Paper variant='outlined' style={{ height: 90, width: 90}} sx={{
        position: 'absolute',
        bottom: 20,
        left: 20
      }}>
        <img alt="qrcode" src={qrcode} style={{ height: '100%', width: '100%' }}/>
      </Paper>
      {/* Drawer */}

      <Drawer anchor="right" open={drawerOpen} onClose={handleDrawerClose}>
        {/* Add your drawer content here */}
        <div style={{ width: '300px' }}>
          <Typography align="center" variant="h6" style={{ margin: '10px' }}>
            RULAR
          </Typography>
          <Divider variant="middle">主体</Divider>
          <List>
            <ListItem>
            <Typography variant="subtitle1" style={{ margin: '10px' }}>
              头套主体面部
            </Typography>
            <ColorSelect
              defaultColor={config["hood-base-color"]}
              setSelectedColor={(color) => {
                handleSelectColor("hood-base-color", color)
              }}
            />
            </ListItem>
            <ListItem>
            <Typography variant="subtitle1" style={{ margin: '10px' }}>
              头盖（主体中间）
            </Typography>
            <ColorSelect
              defaultColor={config["crown-color"]}
              setSelectedColor={(color) => {
                handleSelectColor("crown-color", color)
              }}/>
            </ListItem>
          </List>
          <Divider variant="middle">眉毛</Divider>
          <List>
            <ListItem>
              <Box sx={{ flexGrow: 1 }}>
                <Grid container spacing={2}>
                  <Grid xs={4}>
                    <ItemPicker text="双眉" selected={config['eyebrow-style'] === 'default'} onClick={() => {handleEyeBrowStyleChange('default');}}/>
                  </Grid>
                  <Grid xs={4}>
                    <ItemPicker text="豆豆(+30R)" selected={config['eyebrow-style'] === 'dou'} onClick={() => {handleEyeBrowStyleChange('dou')}}/>
                  </Grid>
                  <Grid xs={4}>
                    <ItemPicker text="焰眉(+30R)" selected={config['eyebrow-style'] === 'frame'} onClick={() => {handleEyeBrowStyleChange('frame')}}/>
                  </Grid>
                </Grid>
              </Box>
            </ListItem>
            <ListItem>
              <Typography variant="subtitle1" style={{ margin: '10px' }}>
                眉毛
              </Typography>
              <ColorSelect
                defaultColor={config["brow-color"]}
                setSelectedColor={(color) => {
                  handleSelectColor("brow-color", color)
              }}/>
            </ListItem>
          </List>
          <Divider variant="middle">面部</Divider>
          <List>
            <ListItem>
              <Typography variant="subtitle1" style={{ margin: '10px' }}>
                鼻梁
              </Typography>
              <ColorSelect
                defaultColor={config["bridge-color"]}
                setSelectedColor={(color) => {
                  handleSelectColor("bridge-color", color)
                }}/>
            </ListItem>
            <ListItem>
              <Typography variant="subtitle1" style={{ margin: '10px' }}>
                鼻头
              </Typography>
              <ColorSelect
                defaultColor={config["nose-color"]}
                setSelectedColor={(color) => {
                  handleSelectColor("nose-color", color)
              }}/>
            </ListItem>
            <ListItem>
              <Typography variant="subtitle1" style={{ margin: '10px' }}>
                脸部
              </Typography>
              <ColorSelect
                defaultColor={config["muzzle-color"]}
                setSelectedColor={(color) => {
                  handleSelectColor("muzzle-color", color)
              }}/>
            </ListItem>
            <ListItem>
              <Typography variant="subtitle1" style={{ margin: '10px' }}>
                嘴唇
              </Typography>
              <ColorSelect
                defaultColor={config["muzzle-lip-color"]}
                setSelectedColor={(color) => {
                  handleSelectColor("muzzle-lip-color", color)
              }}/>
            </ListItem>
            <ListItem>
              <Typography variant="subtitle1" style={{ margin: '10px' }}>
                下巴
              </Typography>
              <ColorSelect
                defaultColor={config["chin-color"]}
                setSelectedColor={(color) => {
                  handleSelectColor("chin-color", color)
              }}/>
            </ListItem>
          </List>
          <Divider variant="middle">脸部细节</Divider>
          <List>
            <ListItem>
              <Box sx={{ flexGrow: 1 }}>
                <Grid container spacing={2}>
                  <Grid xs={4}>
                    <ItemPicker selected={config['muzzle-style'] === 'default' || config['muzzle-style'] === 'added-default'} text="圆脸" onClick={() => {handleMuzzleStyleChange('default');}}/>
                  </Grid>
                  <Grid xs={4}>
                    <ItemPicker selected={config['muzzle-style'] === 'fur' || config['muzzle-style'] === 'added-fur'} text="FUR" onClick={() => {handleMuzzleStyleChange('fur');}}/>
                  </Grid>
                </Grid>
              </Box>
            </ListItem>
            <ListItem>
              <Box sx={{ flexGrow: 1 }}>
                <Grid container spacing={2}>
                  <Grid xs={4}>
                    <ItemPicker selected={config['muzzle-style'].startsWith('added')} text="脸颊(+30R)" onClick={() => {handleMuzzleAddedChange(!config['muzzle-style'].startsWith('added'));}}/>
                  </Grid>
                </Grid>
              </Box>
            </ListItem>
            <ListItem>
              <Box sx={{ flexGrow: 1 }}>
                <Grid container spacing={2}>
                  <Grid xs={4}>
                    <ItemPicker selected={config['muzzle-assoicate'] === 'default'} text="三孔" onClick={() => {handleMuzzleAssoicateChange('default');}}/>
                  </Grid>
                  <Grid xs={4}>
                    <ItemPicker selected={config['muzzle-assoicate'] === 'mustache'} text="胡须(+30R)" onClick={() => {handleMuzzleAssoicateChange('mustache');}}/>
                  </Grid>
                  <Grid xs={4}>
                    <ItemPicker selected={config['muzzle-assoicate'] === 'fur'} text="皮毛(+40R)" onClick={() => {handleMuzzleAssoicateChange('fur');}}/>
                  </Grid>
                </Grid>
              </Box>
            </ListItem>
            <ListItem>
              <Typography variant="subtitle1" style={{ margin: '10px' }}>
                A
              </Typography>
              <ColorSelect
                defaultColor={config["muzzle-a-color"]}
                setSelectedColor={(color) => {
                  handleSelectColor("muzzle-a-color", color)
              }}/>
            </ListItem>
            <ListItem>
              <Typography variant="subtitle1" style={{ margin: '10px' }}>
                B
              </Typography>
              <ColorSelect
                defaultColor={config["muzzle-b-color"]}
                setSelectedColor={(color) => {
                  handleSelectColor("muzzle-b-color", color)
              }}/>
            </ListItem>
          </List>
          <Divider variant="middle">耳朵细节</Divider>
          <List>
            <ListItem>
              <Box sx={{ flexGrow: 1 }}>
                <Grid container spacing={2}>
                  <Grid xs={4}>
                    <ItemPicker selected text="耳朵细节"/>
                  </Grid>
                </Grid>
              </Box>
            </ListItem>
            <ListItem>
              <Typography variant="subtitle1" style={{ margin: '10px' }}>
                A
              </Typography>
              <ColorSelect
                defaultColor={config["ear-a-color"]}
                setSelectedColor={(color) => {
                  handleSelectColor("ear-a-color", color)
              }}/>
            </ListItem>
            <ListItem>
              <Typography variant="subtitle1" style={{ margin: '10px' }}>
                B
              </Typography>
              <ColorSelect
                defaultColor={config["ear-b-color"]}
                setSelectedColor={(color) => {
                  handleSelectColor("ear-b-color", color)
              }}/>
            </ListItem>
            <ListItem>
              <Typography variant="subtitle1" style={{ margin: '10px' }}>
                C
              </Typography>
              <ColorSelect
                defaultColor={config["ear-c-color"]}
                setSelectedColor={(color) => {
                  handleSelectColor("ear-c-color", color)
              }}/>
            </ListItem>
          </List>
          <Divider variant="middle">其他</Divider>
          <List>
            <ListItem>
              <FormGroup>
                <FormControlLabel control={<Checkbox checked={config["ear-hole"]} onChange={(event) => {
                  handleCheckChange("ear-hole", event.target.checked);
              }} />} label="加耳朵孔(+10R)" />
              </FormGroup>
            </ListItem>
            <ListItem>
              <FormGroup>
                <FormControlLabel control={<Checkbox checked={config["glass-hole"]} onChange={(event) => {
                  handleCheckChange("glass-hole", event.target.checked);
              }} />} label="加眼镜孔(+10R)" />
              </FormGroup>
            </ListItem>
            <ListItem>
              <FormGroup>
                <FormControlLabel control={<Checkbox checked={config["texture-nose"]} onChange={(event) => {
                  handleCheckChange("texture-nose", event.target.checked);
              }} />} label="压纹肌理鼻子(+10R)" />
              </FormGroup>
            </ListItem>
            <ListItem>
              <Typography variant="subtitle1" style={{ margin: '10px' }}>
                纽扣改色(+10R)
              </Typography>
              <ColorSelect
                defaultColor={config["muzzle-button"]}
                setSelectedColor={(color) => {
                  handleSelectColor("muzzle-button", color)
              }}
                ignore={["texture-black", "tiffany"]}
              />
            </ListItem>
          </List>
        </div>
        <div style={{ width: '300px', display: 'flex', justifyContent: 'space-around', margin: '20px' }}>
          <Button variant="outlined" onClick={handleDrawerClose} startIcon={<CloseIcon />}>
            关闭
          </Button>
          <SaveLoadPopUp config={config} setConfig={setConfig} />
          <Button variant="outlined" onClick={handleResetToDefault} startIcon={<DeleteIcon />} color="error" style={{}}>
            重置
          </Button>
        </div>
        <div style={{ width: '300px', display: 'flex', justifyContent: 'space-around', margin: '20px' }}>
          <Typography variant="caption" style={{ margin: '10px', color: '#BBBBBB' }}>
            Developed by <a style={{color: '#BBBBBB'}} href='https://twitter.com/pup_bobo'>@pup_bobo</a>. All rights reserved.
          </Typography>
        </div>
      </Drawer>
    </Container>
  );
}

export default App;